body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.pull-left {
  float: left;
}
.clearfix {
  clear: both;
}

.country-container {
  display: block;
  padding: 7px;
  background-color: #eee;
  color: #333;
  margin-bottom: 2px;
  cursor: pointer;
}

.country-container:hover {
  background-color: #e0e0e0;
}

.title {
  font-size: 18px;
  font-weight: bold;
  color: #33f;
}

.selected {
  background-color: #3333ff;
  color: #eee;
}
.selected .title {
  color: #fff;
}
.selected:hover {
  background-color: #3333ff;
  color: #eee;
}

.cities-container {
  border: 1px solid #ccc;
  border-bottom-width: 0;
  border-radius: 3px;
}

.cities-item {
  padding: 7px;
  border-bottom: 1px solid #ccc;
}

.cities-title {
  font-size: 18px;
  font-weight: bold;
  color: grey;
}

.button {
  width: 100px;
  padding: 7px;
  background-color: #fff;
  color: #000;
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin: 7px;
  text-align: 'center';
}
.primary {
  background-color: #33f;
  color: #fff;
}
.button:hover {
  text-decoration: none;
  background-color: #fafafa;
}
.primary:hover {
  text-decoration: none;
  background-color: #44f;
}

.form-container {
  background-color: #f0f0f0;
  padding: 7px;
}

.form-item {
  border-bottom: 1px solid #ccc;
}

.input-field {
  width: 98%;
  margin-top: 10px;
  padding: 5px;
  border: none; 
  border:solid 1px #ccc;
  border-radius: 3px;
}
.input-field:focus {
  border: 1px solid #33f;
}

.actions-container {
  float: right;
  width: 80px;
  text-align: right;
}
.action-item {
  font-size: 11px;
  color: #5555ff;
  text-decoration: underline;
  cursor: pointer;
  margin-right: 5px;
}
.header {
  background-color: #33f;
  color: #eee;
  font-size: 24px;
  font-weight: bold;
  padding: 10px 0;
}
.container {
  width: 80%;
  margin: 0 auto;
}
.countries-column {
  width: 30%;
  float: left;
}
.cities-column {
  width: 65%;
  float: right;
}

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #555;
  padding: 10px 0;
  color: #ccc;
  font-weight: bold;
}


