.header {
  background-color: #33f;
  color: #eee;
  font-size: 24px;
  font-weight: bold;
  padding: 10px 0;
}
.container {
  width: 80%;
  margin: 0 auto;
}
.countries-column {
  width: 30%;
  float: left;
}
.cities-column {
  width: 65%;
  float: right;
}

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #555;
  padding: 10px 0;
  color: #ccc;
  font-weight: bold;
}

